/* flat collapse */

body .panel-group .panel {
	border-radius: 0px;
	border: 2px solid #BDC3C7;
}

body .panel-heading {
	border-top-left-radius: 0px;
	border-top-right-radius: 0px;
	border-bottom: 2px solid #BDC3C7;
	background-color: #BDC3C7 !important;
	box-shadow: 0px !important;
}

body .panel-heading a {
	color: #3B546B;
	font-size: 15px;
}

body .panel-heading a > span:first-child {
	height: 19px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	float: left;
	width: 90%;
}

body .panel-default > .panel-heading {
	border-color: #BDC3C7;
}

/* end flat collapse */



/* masonry */


.masonry-3 {
	-moz-column-count: 3;
	-moz-column-gap: 20px;
	-webkit-column-count: 3;
	-webkit-column-gap: 20px;
	column-count: 3;
	column-gap: 20px;
}

.modal-body .images.masonry-3 {
	max-height: 500px;
	overflow: auto;
}

@media all and (max-width: 1050px) {

	.masonry-3 {
		-moz-column-count: 2;
		-webkit-column-count: 2;
		column-count: 2;
	}

}

@media all and (max-width: 700px) {

	.masonry-3 {
		-moz-column-count: 1;
		-webkit-column-count: 1;
		column-count: 1;
	}

}

.masonry-4 {
	-moz-column-count: 4;
	-moz-column-gap: 20px;
	-webkit-column-count: 4;
	-webkit-column-gap: 20px;
	column-count: 4;
	column-gap: 20px;
}

@media all and (max-width: 1050px) {

	.masonry-4 {
		-moz-column-count: 3;
		-webkit-column-count: 3;
		column-count: 3;
	}

}

@media all and (max-width: 1400px) {

	.masonry-4.users {
		-moz-column-count: 3;
		-webkit-column-count: 3;
		column-count: 3;
	}

}

@media all and (max-width: 1180px) {

	.masonry-4.users {
		-moz-column-count: 2;
		-webkit-column-count: 2;
		column-count: 2;
	}

}

@media all and (max-width: 670px) {

	.masonry-4.users {
		-moz-column-count: 1;
		-webkit-column-count: 1;
		column-count: 1;
	}

}

@media all and (max-width: 1400px) {

	.masonry-4.packages {
		-moz-column-count: 3;
		-webkit-column-count: 3;
		column-count: 3;
	}

}

@media all and (max-width: 1180px) {

	.masonry-4.packages {
		-moz-column-count: 2;
		-webkit-column-count: 2;
		column-count: 2;
	}

}

@media all and (max-width: 670px) {

	.masonry-4.packages {
		-moz-column-count: 1;
		-webkit-column-count: 1;
		column-count: 1;
	}

}

@media all and (max-width: 830px) {

	.masonry-4 {
		-moz-column-count: 2;
		-webkit-column-count: 2;
		column-count: 2;
	}

}

@media all and (max-width: 625px) {

	.masonry-4 {
		-moz-column-count: 1;
		-webkit-column-count: 1;
		column-count: 1;
	}

}

.masonry-5 {
	-moz-column-count: 5;
	-moz-column-gap: 20px;
	-webkit-column-count: 5;
	-webkit-column-gap: 20px;
	column-count: 5;
	column-gap: 20px;
}

@media all and (max-width: 1270px) {

	.masonry-5 {
		-moz-column-count: 4;
		-webkit-column-count: 4;
		column-count: 4;
	}

}

@media all and (max-width: 1050px) {

	.masonry-5 {
		-moz-column-count: 3;
		-webkit-column-count: 3;
		column-count: 3;
	}

}

@media all and (max-width: 840px) {

	.masonry-5 {
		-moz-column-count: 2;
		-webkit-column-count: 2;
		column-count: 2;
	}

}

@media all and (max-width: 560px) {

	.masonry-5 {
		-moz-column-count: 1;
		-webkit-column-count: 1;
		column-count: 1;
	}

}

.masonry-3 > *, .masonry-4 > *, .masonry-5 > * {
	display: block; /* Display inline-block, and absolutely NO FLOATS! */
	margin-bottom: 30px;
	width: 100%;
	-webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
	overflow: hidden;
}

.images.masonry-3 > * {
	margin-bottom: 15px;
}

/* end masonry */



/* sites */


.sites .site {
	border: 2px solid #bdc3c7;
	padding: 20px;
	border-radius: 5px;
}

.sites .site .siteLink {
	height: 27px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

.window {
	margin-bottom: 20px;
}

.elements .window {
	margin-bottom: 10px;
}

.window .top {
	height: 34px;
	background: #E1E0E0;
}

.window .top .buttons {
	float: left;
	height: 13px;
	margin-top: 12px;
	margin-left: 10px;
}

.window .top .buttons > * {
	border-radius: 10px;
	display: block;
	float: left;
	height: 10px;
	margin-right: 6px;
	width: 10px;
}

.window .top .buttons .red {
	background: #e74c3c;
}

.window .top .buttons .yellow {
	background: #f4a62a;
}

.window .top .buttons .green {
	background: #16a085;
}

.window .top b {
	float: right;
	margin-right: 14px;
	line-height: 34px;
	height: 34px;
	text-overflow: ellipsis;
	max-width: 70%;
	overflow: hidden;
	white-space: nowrap;
}

.window .viewport {
	height: auto;
	width: 100%;
}

.window .viewport > img {
	width: 100%;
}

.window .viewport iframe {
	width: 100%;
	border: 0px;
}

.window .viewport .placeHolder img {
	max-width: 100%;
}

.window .viewport .placeHolder:hover {
	color: #1ABC9C;
	font-weight: 400;
}

.window .viewport .placeHolder span {
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 15px;
}

.window .bottom {
	height: 16px;
	background: #E1E0E0;
}

.sites .site .state {
	height: 27px;
}

.sites .site .zoomer-cover a {
	background: none !important;
}

/* end sites */



/* site settings */

.optionPane {
	background: #eee;
	border: 2px dashed #ddd;
	padding: 30px 20px;
	margin-bottom: 15px;
}

.optionPane#siteSettingsPublishing {
	background: none;
}

.optionPane.export {
	padding: 20px;
	background: #fff;
}

.optionPane h6 {
	margin-top: 0px;
	font-weight: bold;
	font-size: 23px;
	margin-bottom: 40px;
}

.optionPane.export h6 {
	font-size: 18px;
	margin-bottom: 20px;
}

.optionPane .url {
	line-height: 36px;
	height: 36px;
	text-overflow: ellipsis;
}

.optionPane.export label.checkbox {
	padding-top: 0px;
	font-size: 20px;
	font-size: 17px;
	line-height: 20px;
	min-height: 20px;
}

.siteSettingsModal .FTP_Connecting {
