	margin-bottom: 0px;
}

.optionPane.export .table td span.publishing {
	color: #000;
	float: right;
	font-size: 12px;
	text-transform: uppercase;
	display: none;
}

.optionPane.export .table td span.publishing .done {
	font-weight: bold;
	display: none;
}

.optionPane.export .table td span.publishing .done > span {
	font-size: 16px;
}

.ftpBrowse {
	display: none;
}

.ftpList {
	border: 2px solid #bdc3c7;
	background: #fff;
}

.ftpList .alert {
	margin: 20px 20px 20px;
}

.ftpList .loaderFtp {
	margin: 20px 20px;
	font-size: 13px;
	text-transform: uppercase;
}

.ftpList .loaderFtp img {
	width: 60px;
	height: 60px;
	margin-right: 20px;
}

.ftpList ul {
	padding: 0px;
	list-style: none;
	list-style-type: none;
	margin: 10px 0px 20px;
}

.ftpList ul li a {
	display: block;
	height: 30px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	padding: 0px 20px;
}

.ftpList ul li a, .ftpList ul li span {
	color: #7F8C8D;
}

.ftpList ul li .link, .ftpList ul li .link span {
	color: #2C3E50;
}

.ftpList ul li .link:hover {
	background: #bdc3c7;
}

#ftpListItems a.close {
	margin: 13px 15px 0 0;
	opacity: 1;
	color: #7F8C8D;
	font-size: 18px;
}

#ftpListItems a.close:hover {
	color: #3D566E;
}

#ftpListItems a.back {
	margin: 10px 0 0 17px;
	float: left;
	text-transform: uppercase;
	font-size: 13px;
	font-weight: bold;
}

#ftpTestAlerts .alert {
	margin-bottom: 0px;
}

.modal-content .loader {
	display: block;
}

.modal-content .loader, .modal-content .loader2 {
	top: 0px;
	width: 100%;
	height: 100%;
	background: #fff;
	z-index: 10000;
	text-align: center;
	font-size: 14px;
	text-transform: uppercase;
	margin: 40px 0px;
}

.modal-content .loader > img, .modal-content .loader2 > img {
	display: block;
	width: 128px;
	height: 128px;
	margin: 0px auto 0px;
}

.hosting_option {
	display: none;
}

.hosting_option p {
	font-size: 14px;
}

.hosting_option .form-group {
	margin-left: 0px;
	margin-right: 0px;
}

.hosting_option .input-group {
	margin-bottom: 20px;
}

/* end site settings */



/* element editor */

.editor {
	margin: 0px 0px 15px;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	height: 600px;
	border: 0px;
	line-height: 20px !important;
}

.previewFrame {
	width: 100%;
	height: 600px;
	border: 0px;
	overflow-y: auto;
}

.errorDrawer {
    padding: 10px 20px;
    font-size: 12px;
    max-height: 400px;
    min-height: 45px;
    overflow-y: auto;
    position: relative;
    display: none;
}

.errorDrawer > button {
    position: absolute;
    top: 10px;
    right: 20px;
}

.errorDrawer p {
    margin-bottom: 0px;
    color: #36495C;
}

.errorDrawer p::before {
    content: ' ';
    color: #C84135;
}

