.images {
	padding: 20px 12px 5px 20px;
	flex: 1 1 auto;
}

.builderUI .images {
	height: 65vh;
	overflow-y: auto;
}

.images .slimWrapper {
	width: calc(16.67% - 11px);
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 10px;
	height: calc(14vh + 22px);
	border-radius: 5px;
	overflow: hidden;
	border: 2px dashed #95A5A6;
}

.images .slimWrapper .slim {
	cursor: pointer;
	height: 100%;
	background-color: #ECF0F1;
}

.images .slimWrapper_ {
	width: calc(16.67% - 11px);
	display: inline-block;
	margin-right: 10px;
	margin-bottom: 10px;
	height: calc(14vh + 24px);
	border-radius: 5px;
	overflow: hidden;
	border: 2px solid #ddd;
	padding: 10px;
}

.images .slimWrapper_ .slim {
	cursor: pointer;
	max-height: 100%;
	background-color: #fff;
}

.images .slim .slim-area {
	height: 100%;
	display: flex;
	justify-content: center;
}

.images .slim .slim-area .slim-upload-status {
	left: 1em;
	white-space: normal;
}

.images .slim .slim-area .slim-label p {
	font-size: 14px;
}

.images .slim .slim-area .slim-label b {
	display: block;
	font-size: 32px;
	line-height: 40px;
}

.images .image {
	padding: 10px;
	border: 2px solid #ddd;
	display: inline-block;
	width: calc(16.67% - 11px);
	margin-right: 8px;
	margin-bottom: 8px;
	cursor: pointer;
	border-radius: 5px;
	transition: border-color .25s ease-in-out,
				background-color .25s ease-in-out;
	overflow: hidden;
}

.images .image.active, .images .image:hover {
	border-color: #2F81B7;
	background-color: rgba(47, 129, 183, .1);
}

.images .image .imageWrap {
	width: 100%;
	height: 14vh;
	overflow: hidden;
	text-align: center;
	/*position: relative;*/
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
}

.images .image .imageWrap img {
	height: 14vh;
}

.images .image .buttons {
	margin-top: 5px;
}

.images .image .buttons > * {
